//       
// Firebase        

import { useEffect, useState } from "react";
import { addDoc, collection, getDocs } from "firebase/firestore";
import { db } from "../js/firebaseConfig";

//    CSS  import
import "../css/user_form.scss"; // CSS  import

const UserFormList = () => {
  // [1]   //////////////
  //   
  const [userName, setUserName] = useState("");
  //    ()
  const [userAge, setUserAge] = useState(0);
  //   
  const [userAddr, setUserAddr] = useState("");
  //     
  const [userList, setUserList] = useState([]);

  // [2]     //////
  //     
  const getUserList = async () => {
    // async/await    .
    //   async  .
    //  'users'    
    const allCollection = await getDocs(collection(db, "users"));
    // await   
    // Promise   .
    // getDocs Firestore   .
    // collection Firestore   .
    // 'users'     
    // collection(db, 'users') .

    //    
    //       
    // !
    const userListArray = allCollection.docs.map((doc) => {
      console.log(doc.id, " => ", doc.data());
      return { id: doc.id, ...doc.data() };
    });

    //     !
    setUserList(userListArray);
    // setUserList   .
  }; //     //////////////

  // [3]    //////////////
  const addUser = async () => {
    // async/await    .
    //   async  .

    //     
    //    
    if (userName && userAge) {
      //  'users'    
      await addDoc(collection(db, "users"), {
        name: userName,
        age: Number(userAge),
        //    .
        addr: userAddr,
      });
      // addDoc Firestore   .
      // collection Firestore   .
      // 'users'     
      // collection(db, 'users') .

      //     ///
      setUserName("");
      setUserAge(0);
      setUserAddr("");

      //      ///
      getUserList();
      //       !
    } /// if ////
  }; //    //////////////

  // [4]     /////////////
  useEffect(() => {
    //   DB   
    getUserList();
  }, []); //      .
//   }, [userList]); ->    !

  //   //////////////
  return (
    <div className="user-form">
      <h2>Firebase Users</h2>
      {/*    */}
      <label htmlFor="unm">:</label>
      <input
        id="unm"
        type="text"
        placeholder=" "
        value={userName}
        onChange={(e) => setUserName(e.target.value)}
        // onChange     .
      />
      {/*    */}
      <label htmlFor="age">:</label>
      <input
        id="age"
        type="number"
        placeholder=" "
        value={userAge}
        onChange={(e) => setUserAge(e.target.value)}
        // onChange     .
      />

      {/*    */}
      <label htmlFor="addr">:</label>
      <input
        id="addr"
        type="text"
        placeholder=" "
        value={userAddr}
        onChange={(e) => setUserAddr(e.target.value)}
        // onChange     .
      />

      {/*    */}
      <button onClick={addUser}> </button>

      <br />
      <br />
      <hr />

      {/*    */}
      <div className="user-list">
        <h2> </h2>
        <ul>
          {userList.map((user) => (
            <li key={user.id}>
              {user.name} ({user.age}) - {user.addr}
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default UserFormList;
